import React, { useState, useEffect } from 'react';
import { useAuthDispatch, fetchData, useAuthState } from '../../../../context';
import * as Styled from '../../../../components';
import * as utils from '../../../../utils';

export const ViewTimeline = (props) => {
  const dispatch = useAuthDispatch();
  const { view } = useAuthState();
  const [timeline, setTimeline] = useState([]);
  function getItem(i) {
    if (timeline[i].assetId)
      return truncate(
        `${timeline[i].assetId.description} ${timeline[i].itemId.item}`,
        40
      );
    return truncate(timeline[i].itemId.item, 40);
  }
  function truncate(text, i) {
    let len = text.length;
    if (len < i) return text;
    return text.substring(0, i) + '..';
  }
  useEffect(() => {
    const doGet = async () => {
      let query = utils.remoteuri.timeline + `?self=${true}`;
      if (view === 'timeline') query = query + `&today=${true}`;
      else query = query + `&today=${false}`;
      let data = await fetchData(dispatch, query);
      if (data) setTimeline(data.issued);
    };
    doGet();
  }, [dispatch, view]);
  return (
    <Styled.VetView>
      <Styled.PairStruct>
        <Styled.CustomCCont>
          <Styled.NumBox>No.</Styled.NumBox>
          <Styled.DateBox>Date</Styled.DateBox>
          <Styled.ItemBox>Item description/ Service</Styled.ItemBox>
          <Styled.QuantBox> Quantity</Styled.QuantBox>
          <Styled.StatusBox>Status</Styled.StatusBox>
        </Styled.CustomCCont>
        {timeline.map((request, i) => (
          <div key={i}>
            <Styled.Table key={i}>
              <Styled.TableB>
                <Styled.CCont>
                  <Styled.NumBox>{i}</Styled.NumBox>
                  <Styled.DateBox>
                    {new Date(request.issuedAt).toLocaleString()}
                  </Styled.DateBox>
                  <Styled.ItemBox>{getItem(i)}</Styled.ItemBox>
                  <Styled.QuantBox>
                    {request.quantity ? request.quantity : 1}
                  </Styled.QuantBox>
                  <Styled.StatusBox>
                    {request.approved ? 'approved' : 'pending'}
                  </Styled.StatusBox>
                </Styled.CCont>
              </Styled.TableB>
            </Styled.Table>
          </div>
        ))}
      </Styled.PairStruct>
    </Styled.VetView>
  );
};
