import Styled from 'styled-components';
import { CCont } from './client';
import { device } from './device';
export const VetView = Styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 85%;
    flex: 1 1 auto;
`;
export const Li = Styled.li`
	margin: 0.5rem;
    padding: .5rem .2rem;
	border: 1px solid white;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
	border-bottom: 1px solid;
	:hover {
		background-color: #e6e6e6;
		cursor: pointer;
	}
	:active {
		color: green;
	}
`;
export const Ol = Styled.ol`
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.05);
	border-top: 5px solid lightblue;
    border-radius: 5px;
`;
export const SuccessCard = Styled.p`
   text-align: center;
   color: green;
   font-style: italic;
`;
export const CustomCCont = Styled(CCont)`
  @media ${device.mobileS}{
	  font-size: 12px;
  }
  @media ${device.laptop}{
	font-size: 18px;
    font-weight: bolder;
	padding: .5rem .2rem;
  }
`;
export const NumBox = Styled.div`
   @media ${device.mobileS}{
	width: 2%;
  }
  @media ${device.laptop}{
    width: 5%;
  } 
`;
export const DateBox = Styled.div`
  @media ${device.mobileS}{
	width: 25%;
  }
  @media ${device.laptop}{
    width: 25%;
  } 
`;
export const ItemBox = Styled.div`
   @media ${device.mobileS}{
	width: 30%;
  }
  @media ${device.laptop}{
    width: 50%;
  } 
`;
export const QuantBox = Styled.div`
   @media ${device.mobileS}{
	width: 5%;
  }
  @media ${device.laptop}{
    width: 10%;
  } 
`;
export const StatusBox = Styled.div`
   width: 10%;
`;
